export function serialize(obj, prefix) {
    const str = []
    for (const key in obj) {
        if (obj.hasOwnProperty(key)) {
            const k = prefix ? `${prefix}[${key}]` : p
            const v = obj[key]
            str.push(typeof v === 'object' ? serialize(v, k) : `${encodeURIComponent(k)}=${encodeURIComponent(v)}`)
        }
    }
    return str.join('&')
}

