export function parseDate(date, cFormat) {
    let format = cFormat || 'y-m-d h:i:s';
    let formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1,
        d: date.getDate(),
        h: date.getHours(),
        i: date.getMinutes(),
        s: date.getSeconds(),
        a: date.getDay()
    };
    const time_str = format.replace(/([ymdhisa])+/g, (result, key) => {
        let value = formatObj[key];
        if (key === 'a') {
            return ['æ¥', 'ä¸', 'äº', 'ä¸', 'å', 'äº', 'å­'][value]
        }
        return value.toString().padStart(2, '0');
    });
    return time_str;
}

/**
 * æç´¢æ¡æç´¢è¾å¥ãåªéç¨æ·æåä¸æ¬¡è¾å¥å®ï¼ååéè¯·æ±
 * ææºå·ãé®ç®±éªè¯è¾å¥æ£æµ
 * çªå£å¤§å°Resizeãåªéçªå£è°æ´å®æåï¼è®¡ç®çªå£å¤§å°ãé²æ­¢éå¤æ¸²æ
 * @param {*} func
 * @param {*} delay
 * @param {*} immediate
 * @returns
 */
export const debounce = (func, delay, immediate) => {
    let later = null
    return (...args) => {
        if (!later && immediate) {
            func(...args)
        }
        later && clearTimeout(later)
        later = setTimeout(func, delay, ...args)
    }
}

