/**
 * æ»å¨å è½½ï¼å è½½æ´å¤ææ»å°åºé¨çå¬
 * è°·æ­æç´¢æ¡ï¼æç´¢èæ³åè½
 * é«é¢ç¹å»æäº¤ï¼è¡¨åéå¤æäº¤
 * @param {*} func
 * @param {*} delay
 * @returns
 */
export const throttle = (func, delay) => {
    let later = null
    return (...args) => {
        if (later) return
        later = setTimeout(() => {
            func(...args)
            later = null
        }, delay)
    }
}

/**
 * eventEmitterå®ç°
 */
export class EventEmitter {
    constructor(events) {
        this.events = events || {}
    }
    subscribe(name, cb) {
        (this.events[name]) || (this.events[name] = []).push(cb)
        return {
            unsubscribe: () => {
                this.events[name] && this.events[name].splice(this.events[name].indexOf(cb) >>> 0, 1)
            }
        }
    }
    emit(name, ...args) {
        (this.events[name] || []).forEach(fn => fn(...args))
    }
}

/**
 * composeå®ç°
 * compose (widthData(), widhtLogger())(Component)
*/

export const compose = (...fns) => (Component) => {
    return fns.reduceRight((Component, fn) => {
        return fn(Component)
    }, Component)
}

/**
 * å½æ°æ¯éå
 */
export const curring = (func) => (...args) => {
    if (func.length <= args.length) {
        return func(...args)
    }
    return (...args2) => curring(func)(...args, ...args2)
}

